import {BrowserRouter as Router, Routes, Route} from "react-router-dom"

import React from 'react'
import RenterComponent from "./Components/RenterComponent"
import ViewListOfUsers from "./Components/ViewListOfUsers"


function App() {
  return (
    <Router>
    <Routes>
        <Route path = "/" element = {<RenterComponent/>}/>
        <Route path = "/transaction" element = {<ViewListOfUsers/>}/>
    </Routes>
    </Router>
  )
}

export default App