import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "font-awesome/css/font-awesome.css";
import {useEffect, useState} from "react"
import axios from "axios";
import Navbar from "./Navbar";



function ViewListOfUsers() {
const [data, setData] = useState([])
useEffect(()=>{

 const  getAllTransaction =  async ()=>{

  const result = await axios.get("https://renter-s-app.herokuapp.com/renter/transaction");
  setData(result.data.msg)

 }
 getAllTransaction();
},[])

  return (
    <>

    <Navbar/>
    <div className = "container text-center mt-3 mb-4">
      
      <h3 className ="textcenter m-3">
      Sample Transaction Output
      </h3>
  
     <table class="table table-primary">
  <thead>
    <tr>
      <th scope="col">#</th>
      <th scope="col">AccomodationStatus</th>
      <th scope="col">RentAmount</th>
      <th scope="col">MonthlyIncome</th>
      <th scope="col">PaymentPlan</th>
    </tr>
  </thead>
  <tbody>
    
    {data.map((val, id=0)=>(
      <tr key ={val._id}>
      <td>{++id}</td>
      <td>{val.accomodationStatus}</td>
      <td>{val.rentAmount}</td>
      <td>{val.monthlyIncome}</td>
      <td>{val.paymentPlan}</td>
      </tr>
    ))}
    <tr>
      
    </tr>
  </tbody>
</table>
</div>
    </>
  );
}

export default ViewListOfUsers;
