import React from "react";
import "bootstrap/dist/css/bootstrap.min.css";
import "font-awesome/css/font-awesome.css";
import Navbar from "./Navbar";
import styles from "../styles/RenterComponent.module.css";
import { useState } from "react";
import { CircularProgressbar } from "react-circular-progressbar";
import "react-circular-progressbar/dist/styles.css";
import CurrencyFormat from "react-currency-format";
import axios from "axios";

function RenterComponent() {
  const [getTextValue, setGetText] = useState("");
  const [monthlyPay, setMonthlyPay] = useState(Number);
  const [requestMoney, setRequestMoney] = useState(Number);
  const [selectOption, setSelectOption] = useState("");
  const [alert, setAlert] = useState("");
  const [progress, setprogress] = useState(33.33);
  const [count, setCount] = useState(1);
  const [isClick, setIsClick] = useState(false);

  function getText(e) {
    setGetText(e.currentTarget.textContent);
    e.target.style.opacity = 0.9;
    if (e.target.style.opacity === 0.9) {
      e.target.style.opacity = 0.5;
    } else {
      e.target.style.opacity = 0.9;
    }
  }
  function monthlyPayment(e) {
    setMonthlyPay(e.target.value);
  }

  function requestAmount(e) {
    setRequestMoney(e.target.value);
  }

  const submitSelection = () => {
    if (
      getTextValue === "" ||
      monthlyPay === 0 ||
      requestMoney === 0 ||
      selectOption === ""
    ) {
      return setAlert("Fill the form and try again!!");
    }
   

    setprogress(progress * 2);
    if (count !== 3) {
      setCount(count + 1);
    }

    setIsClick(true);
  };

  function selectValue(e) {
    setSelectOption(e.target.value);
  }

  return (
    <React.Fragment>
      <Navbar />

      {isClick ? (
        <div className="container mt-5">
          <div className="row">
            <div className="col-md-3"></div>
            <div className="col-md-6">
              <div
                className="card p-2 p-5"
                style={{
                  borderLeft: "2px solid #ccc",
                  borderRight: "2px solid #ccc",
                  borderTop: "1px solid #fff",
                  borderBottom: "2px solid #ccc",
                }}
              >
                <h4 className="mb-5">My Rent</h4>
                <h5 className="mt-3">Payment BreakDown</h5>
                <p>Rent payment amount</p>
                <div className="form-floating mb-3">
                  <input
                    onChange={(e) => requestAmount(e)}
                    value={requestMoney}
                    type="number"
                    className="form-control"
                    id="floatingInput"
                    placeholder="amount@example.com"
                  />
                  <label htmlFor="floatingInput">Amount</label>
                </div>
                <select
                  onChange={(e) => selectValue(e)}
                  className="form-select p-3"
                >
                  <option value="1 Month">1 Month</option>
                  <option value="2 Months">2 Months</option>
                  <option value="3 Months">3 Months</option>
                  <option value="4 Months">4 Months</option>
                  <option value="5 Months">5 Months</option>
                  <option value="6 Months">6 Months</option>
                  <option value="7 Months">7 Months</option>
                  <option value="8 Months">8 Months</option>
                  <option value="9 Months">9 Months</option>
                  <option value="10 Months">10 Months</option>
                  <option value="11 Months">11 Months</option>
                  <option value="12 Months">12 Months</option>
                </select>
                <p className="mt-4">Payment option</p>

                <div className="card">
                  <div className="card-body" style={{ background: "#b2b3b5" }}>
                    <div className="d-flex justify-content-between ps-3 pe-3 mb-2">
                      <h5> Pre-Approved Amount</h5>
                      <p>
                        &#8358;
                        <CurrencyFormat
                          value={requestMoney}
                          displayType={"text"}
                          thousandSeparator={true}
                        />
                      </p>
                    </div>
                    <div className="d-flex justify-content-between ps-3 pe-3 mb-2">
                      <h5>Monthly payment</h5>
                      <p>
                        &#8358;
                        <CurrencyFormat
                          value={
                            Number(requestMoney / 2) +
                            Number(requestMoney * 0.02)
                          }
                          displayType={"text"}
                          thousandSeparator={true}
                        />
                      </p>
                    </div>
                    <div className="d-flex justify-content-between ps-3 pe-3">
                      <h5>Tenor</h5>
                      <p>{selectOption}</p>
                    </div>
                  </div>
                </div>
                <button
                onClick = {saveAcceptedData}
                  className="btn mt-3 btn-lg"
                  style={{ background: "indigo", color: "#fff" }}
                >
                  ACCEPT
                </button>
              </div>
            </div>
            <div className="col-md-3"></div>
          </div>
        </div>
      ) : (
        <div className={styles["renterContainer"]}>
          <div className={styles["content"]}>
            <h5>My Rent</h5>
            <div className={styles["content-body"]}>
              <div className={styles["title"]}>
                <h6>Payment Options</h6>
                <div className={styles["progressbar"]}>
                  <span>{count}of 3</span>
                  <div style={{ width: 40, height: 40, color: "#71e471" }}>
                    <span></span> <CircularProgressbar value={progress} />
                  </div>
                </div>
              </div>
              <span style={{ color: "red", paddingLeft: "60px" }}>{alert}</span>
              <h5>What's your accomodation status?</h5>
              <p onMouseDown={(e) => getText(e)}>Looking to renew my rent</p>
              <p onClick={(e) => getText(e)}>Want to pay for a new place?</p>
              <p onClick={(e) => getText(e)}>I'm still searching</p>
              <p onClick={(e) => getText(e)}>Looking to renew my rent</p>
              <h5>How much is your rent request amount?</h5>
              <input
                type="number"
                placeholder="amount"
                onChange={(e) => requestAmount(e)}
                className={styles["input"]}
              />
              <h5>How much do you earn monthly?</h5>
              <input
                type="number"
                placeholder="amount"
                onChange={(e) => monthlyPayment(e)}
                className={styles["input"]}
              />
              <h5>Choose a monthly payment option</h5>
              <select onChange={(e) => selectValue(e)}>
              <option value="1 Month">1 Month</option>
                  <option value="2 Months">2 Months</option>
                  <option value="3 Months">3 Months</option>
                  <option value="4 Months">4 Months</option>
                  <option value="5 Months">5 Months</option>
                  <option value="6 Months">6 Months</option>
                  <option value="7 Months">7 Months</option>
                  <option value="8 Months">8 Months</option>
                  <option value="9 Months">9 Months</option>
                  <option value="10 Months">10 Months</option>
                  <option value="11 Months">11 Months</option>
                  <option value="12 Months">12 Months</option>
              </select>
              <input type="submit" onClick={submitSelection} value="NEXT" />
            </div>
          </div>
        </div>
      )}
    </React.Fragment>
  );


  //saveAcceptedData


  async function saveAcceptedData(){

    const renterData = {

      accomodationStatus: getTextValue,
      rentAmount: requestMoney,
      monthlyIncome: monthlyPay,
      paymentPlan: selectOption,
    };

    try{
      
     const result =  await axios.post("https://renter-s-app.herokuapp.com/renter/renter-detail",renterData);
     

      if(result)
       console.log(result);

      window.location = "/";

    }catch(err){
      console.log(err)
    }

  }
}

export default RenterComponent;
